      // @parme3接诊范围
      // **//
      postExpert: function (parme,parme2,parme3) {
       var parmes={};
        var url='/api/organization/employees';
        if(parme.type==1){
          //新增医生 接口params
         parmes.employeeDto=parme;
       }else {
          //新增专家
         if(parme){
           parmes.employeeDto=parme;
         }
         if(parme2){
           parmes.iservicePrice=parme2;
         }
         if(parme3){
           parmes.serviceArea=parme3;
         }
       }
        parmes=JSON.stringify(parmes);
        this.$http.post(url,parmes,{
          headers:{
            Authorization:'bearer' + this.token
          },
          emulateJSON:true
        }).then(function(res){
          res =res.body;
          this.success("资料提交成功！")
          //显示展示页面
          this.saveStatus=false;
          //获取员工编号并渲染
          this.fromDat.euNumber=res.employeeDto.euNumber;
          //获取员工id，用来修改资料
          this.expertId=res.employeeDto.usersId;
          this.fromDat.id=res.employeeDto.id;
          this.fromDat.userId=res.employeeDto.usersId;
          if(res.employeeDto.endDate){
            this.fromDat.endDate=publicFn.publicLo.timeCheng(res.employeeDto.endDate);
            this.fromDat.endDate= this.fromDat.endDate.ymd
          }
          if(res.iservicePrice){
            this.ervicePrice();
          }
//          if(res.iservicePrice){
//            for(let k in res.iservicePrice){
//              console.log(res.iservicePrice[k].type,"res.iservicePrice[k].type");
//              console.log(res.iservicePrice[k].id,"res.iservicePrice[k].id");
//              if(res.iservicePrice[k].type==0){
//                this.fromDat.priceAId=res.iservicePrice[k].id;
//                console.log(this.fromDat.priceAId,"this.fromDat.priceAId")
//              }
//              if(res.iservicePrice[k].type==1){
//                this.fromDat.priceVId=res.iservicePrice[k].id;
//                console.log(this.fromDat.priceVId,"this.fromDat.priceAId")
//              }
//            }
//          }
          //接诊范围
          res = res.serviceArea;
          this.cityList=[];
          for(let i in res){
            this.cityList.push({ province:res[i].province,city:res[i].city,area:res[i].county})
          }
        },err=>{
        })
      },
      //上传图片
      postImg: function () {
        //声明变量that，方便senImg方法回调中使用vue的this
        var that = this;
        var imgSrc = publicFn.publicLo.senImg("fileByReservation", function (e) {
          var url='/api/basic/employees/upload';
          //获取e.target.result，（e.target.resul为base64图片流）
          var image={imageUrl:e.target.result};
          image=JSON.stringify(image);
          that.$http.post(url,image,{
            headers:{
              Authorization:'bearer '+that.token
            },
            emulateJSON:true
          }).then(function(res){
            res=res.body;
            //渲染图片
            that.fromDat.imageUrl= res.path[0]
          }).catch(function (res) {
            this.warning(res.message)
          });
        });
