          }
          if(this.fromDat.userType==1){
            this.fromDat.priceA='1';
            this.fromDat.priceV='1';
            this.fromDat = fromDats;
          }else {
            this.ervicePrice(fromDats)
          }
//          this.fromDat = fromDats;
          //查询服务价格

        })
