      //价格详情
      ervicePrice(data){
        var url="/api/assist-order/servicePrice/"+this.expertId
        this.$http.get(url,{
          headers:{
            Authorization:'bearer '+this.token
          }
        }).then(function(res){
            var res=res.body;
            this.price=res;
            if(data){
              this.fromDat=data
            }
            for(let i in res){
              if(res[i].type==0){
                //视频面诊
                this.fromDat.priceAId=res[i].id;
                this.fromDat.priceA=res[i].price+'';
              }
              if(res[i].type==1){
                //图问面诊
                this.fromDat.priceVId=res[i].id;
                this.fromDat.priceV=res[i].price + '';
              }
            }
        }).catch(function () {

        });
