      warning:function(text) {
        this.$message({
          message: text,
          type: 'warning'
        });
      }
    },
    components: {
      CommonHeader: CommonHeader,
      Set:Set,
      NavTop1:NavTop1,
      PatientList:PatientList,
      toking:Toking
    }
  }
</script>



